.news {
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    .themes {
        width: 100%;
        margin: 0 auto;
        .item {
            padding: 15px;
            margin-bottom: 10px;
            min-height: 70px;
            background-color: #fff;
            display: flex;
            align-items: center;
            box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.25);
            .content {
                flex: 1 1 auto;
                .data {
                    display: inline;
                    margin-right: 20px;
                    font-size: 16px;
                    color: #848484;
                }
                p {
                    display: inline;
                }
                .theme {
                    margin-bottom: 10px;
                    font-size: 18px;
                    font-weight: bold;
                    color: #000;
                }
            }
            span {
                flex: 0 0 110px;
                font-weight: bold;
            }
            .more {
                text-align: right;
                font-weight: bold;
                color: #000;
                flex: 0 0 130px;
                background-size: cover !important;
                a {
                    color: #000;
                }
                img {
                    margin-left: 5px;
                }
            }
        }
    }

    .btn-add-post {
        margin: 0 0 20px 0;
        width: 299px;
        height: 50px;
        background: #d6d6d6;
        border: 1px solid #ccc;
        box-shadow: 0px 4px 0 0px rgba(0, 0, 0, 0.45);
        border-radius: 20px;
        font-size: 18px;
        font-weight: bold;
        line-height: 22px;
        color: #686767;
        display: flex;
        align-items: center;
        justify-content: center;
        text-transform: uppercase;
        letter-spacing: 2px;
        &:hover {
            color: #000;
            text-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
            transform: translateY(4px);
            background: #76cdee;
            box-shadow: 0 0 0 1px rgba(122, 122, 122, 0.5);
        }
    }
}
