.wrapper {
    // overflow: hidden;
    width: 100%;
    margin: 0 auto;
    .item {
        background: #fff;
        position: relative;
        height: 450px;
        .body {
            background-size: cover !important;
            opacity: 0.3;
            position: absolute;
            top: 0;
            right: 0;
            width: 100%;
            height: 100%;
            z-index: 999;
        }
        .content {
            z-index: 1000;
            background: none;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            .news {
                padding: 40px;
                flex: 1 0 auto;
                display: flex;
                align-items: center;
                flex-direction: column;
                justify-content: center;
                .title {
                    text-align: center;
                    margin-bottom: 40px;
                    font-size: 2em;
                    text-transform: uppercase;
                    font-weight: 700;
                    letter-spacing: 3px;
                    color: #3d3d3d;
                    text-shadow: 0px 2px 6px #fff;
                }
                .text {
                    text-align: center;
                    color: #5e5e5e;
                    font-size: 1.3em;
                }
            }
            .footer {
                padding: 10px;
                flex: 0 0 65px;
                background: #fff;
                display: flex;
                align-items: center;
                justify-content: space-evenly;
                border-top: 2px solid rgb(158, 158, 158);
                .author {
                    font-weight: 500;
                    font-size: 1.3em;
                    color: #5e5e5e;
                }
                img {
                    height: 50px;
                }
            }
        }
    }
}



.slick-prev {
    background-color: red;
}
